                          'elementType': 'geometry',
                          'stylers': {
                            'color': '#000000'
                          }
                        },
                        {
                          'featureType': 'railway',
                          'elementType': 'geometry.fill',
                          'stylers': {
                            'color': '#000000'
                          }
                        },
                        {
                          'featureType': 'railway',
                          'elementType': 'geometry.stroke',
                          'stylers': {
                            'color': '#08304b'
                          }
                        },
                        {
