                          'elementType': 'labels.text.stroke',
                          'stylers': {
                            'color': '#000000'
                          }
                        },
                        {
                          'featureType': 'building',
                          'elementType': 'geometry',
                          'stylers': {
                            'color': '#022338'
                          }
                        },
                        {
                          'featureType': 'green',
                          'elementType': 'geometry',
                          'stylers': {
                            'color': '#062032'
                          }
                        },
                        {
                          'featureType': 'boundary',
                          'elementType': 'all',
                          'stylers': {
